___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: Arial, Helvetica, sans-serif;
}

header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.spinner {
  width: 25px;
  height: 25px;
}

.btn {
  margin-left: auto;
  padding: 10px;
  color: rgb(119, 119, 119);
  border: 1px solid rgb(119, 119, 119);
  background-color: white;
  border-radius: 10px;
  cursor: pointer;
}

.ticket_btn {
  width: 25px;
  height: 25px;
  background-color: white;
  border: 1px solid rgb(119, 119, 119);
  border-radius: 50%;
  color: rgb(119, 119, 119);
  cursor: pointer;
}

.ticket_status {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  min-width: 25px;
  height: 25px;
  margin: 0;
  border: 1px solid rgb(119, 119, 119);
  border-radius: 50%;
  outline: none;
  cursor: pointer;
  position: relative;
  vertical-align: middle;
}

.ticket_status:checked {
  background: white;
  border-color: rgb(119, 119, 119);
}

.ticket_status:checked::after {
  content: '✓';
  position: absolute;
  left: 5px;
  top: 3px;
  color: rgb(119, 119, 119);
  font-size: 16px;
}

.form {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;

  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  min-width: 200px;
  padding: 10px 20px;
  border: 1px solid rgb(119, 119, 119);
  background-color: white;
  border-radius: 10px;

  font-family: inherit;
  color: rgb(119, 119, 119);
}

.form_name {
  color: inherit;
}

.form_description {
  font-family: Arial, Helvetica, sans-serif;
  color: rgb(119, 119, 119);
}

.button_container {
  display: flex;
  flex-direction: row;
  margin-left: auto;
  gap: 10px;
}

.tickets_container {
  display: flex;
  flex-direction: column;
  gap: 1px;

  color: rgb(119, 119, 119);
}

.ticket_item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  gap: 10px;
  padding: 10px;
  border: 1px solid rgb(119, 119, 119);
  border-radius: 10px;
}

.ticket_main_content {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 60%;
}`, "",{"version":3,"sources":["webpack://./src/css/style.css"],"names":[],"mappings":"AAAA;EACE,yCAAyC;AAC3C;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,8BAA8B;EAC9B,mBAAmB;EACnB,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,YAAY;AACd;;AAEA;EACE,iBAAiB;EACjB,aAAa;EACb,yBAAyB;EACzB,oCAAoC;EACpC,uBAAuB;EACvB,mBAAmB;EACnB,eAAe;AACjB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,uBAAuB;EACvB,oCAAoC;EACpC,kBAAkB;EAClB,yBAAyB;EACzB,eAAe;AACjB;;AAEA;EACE,wBAAwB;EACxB,qBAAqB;EACrB,gBAAgB;EAChB,eAAe;EACf,YAAY;EACZ,SAAS;EACT,oCAAoC;EACpC,kBAAkB;EAClB,aAAa;EACb,eAAe;EACf,kBAAkB;EAClB,sBAAsB;AACxB;;AAEA;EACE,iBAAiB;EACjB,gCAAgC;AAClC;;AAEA;EACE,YAAY;EACZ,kBAAkB;EAClB,SAAS;EACT,QAAQ;EACR,yBAAyB;EACzB,eAAe;AACjB;;AAEA;EACE,eAAe;EACf,QAAQ;EACR,SAAS;EACT,gCAAgC;EAChC,UAAU;;EAEV,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,iBAAiB;EACjB,kBAAkB;EAClB,UAAU;EACV,gBAAgB;EAChB,kBAAkB;EAClB,oCAAoC;EACpC,uBAAuB;EACvB,mBAAmB;;EAEnB,oBAAoB;EACpB,yBAAyB;AAC3B;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,yCAAyC;EACzC,yBAAyB;AAC3B;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,iBAAiB;EACjB,SAAS;AACX;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,QAAQ;;EAER,yBAAyB;AAC3B;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,8BAA8B;EAC9B,uBAAuB;EACvB,SAAS;EACT,aAAa;EACb,oCAAoC;EACpC,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,UAAU;AACZ","sourcesContent":["body {\n  font-family: Arial, Helvetica, sans-serif;\n}\n\nheader {\n  display: flex;\n  flex-direction: row;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 10px;\n}\n\n.spinner {\n  width: 25px;\n  height: 25px;\n}\n\n.btn {\n  margin-left: auto;\n  padding: 10px;\n  color: rgb(119, 119, 119);\n  border: 1px solid rgb(119, 119, 119);\n  background-color: white;\n  border-radius: 10px;\n  cursor: pointer;\n}\n\n.ticket_btn {\n  width: 25px;\n  height: 25px;\n  background-color: white;\n  border: 1px solid rgb(119, 119, 119);\n  border-radius: 50%;\n  color: rgb(119, 119, 119);\n  cursor: pointer;\n}\n\n.ticket_status {\n  -webkit-appearance: none;\n  -moz-appearance: none;\n  appearance: none;\n  min-width: 2